.onboarding {
	// Layout
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 100%;
	padding: 8px;
	// Style
	background-color: #FFF;


	p{
		display: flex;
		padding: 16px;
		min-height: 72px;
		flex-direction: column;
		justify-content: center;
		text-align: center;
	}

	button{
		// Layout
		display: inline-block;
		margin: auto;
		padding: 8px 16px;
		// Style
		border-radius: 8px;
		background-color: rgba(0, 90, 224, 0);
		//Text
		color: #005AE0;
		font-weight: 600; //Should be 700 (bold), but too strong

		&:focus{
			background-color: rgba(0, 90, 224, 0.12);
		}

		&[disabled]{
			color:#989898;
		}
		&[disabled]:first-of-type{
			background-color: rgba(152, 152, 152, 0.12);
		}
	}
}

.SolarSystem{
	display: flex;
	position:relative;
	flex-direction: column;
	align-items: center;
	align-content: center;
	justify-content: center;
	flex:1;

	//Earth
	& > div:first-child{
		position: absolute;
		bottom: 16px;
		left: 50%;
		transform: translateX(-50%);

		width: 208px;
		height: 208px;

		border-radius: 50%;
		background-color:#0071DE;
		background-image: url(./assets/mapmonde.svg);
		background-position: 0 center;

		animation-name: worldtour;
		animation-duration: 24s;
		animation-delay: 150ms;
		animation-timing-function: linear;
		animation-iteration-count: infinite;

	}
}

.GetPositionButton{
	position: absolute;
	bottom:20px;
	right: 16px;

	width: 56px;
	height: 56px;
	padding: 12px;

	background-color:#FFF;
	border-radius: 50%;
	box-shadow: rgba(0,0,0,0.16) 0 0 0 6px;

	svg{
		width: 32px;
		height:32px;
	}
}

@keyframes worldtour {
  from {background-position: 0 center;}
  to {background-position: -480px center;}
}
